import React from 'react';
import { graphql, PageProps } from 'gatsby';
import { MarkdownRemark } from '../types';

import Container from '@mui/joy/Container';
import Link from '../components/Link';

export default function IndexPage({ data }: PageProps<Queries.IndexPageQuery>) {
  const { title, description } = data.site!.siteMetadata!;
  const posts = data.allMarkdownRemark!;

  return (
    <Container>
      <div>
        <h1>{title}</h1>

        <h4>{posts.totalCount} Posts</h4>

        {posts.edges.map(({ node }: { node: MarkdownRemark }) => (
          <div key={node.id}>
            <h3 style={{ marginBottom: 5 }}>
              <Link to={'blog/' + (node.frontmatter?.slug || '')}>{node.frontmatter?.title}</Link>
              <span style={{ color: '#bbb' }}>— {node.frontmatter && node.frontmatter.date}</span>
            </h3>
            <p>{node.excerpt}</p>
          </div>
        ))}
      </div>
    </Container>
  );
}

export const query = graphql`
  query IndexPage {
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
            slug
          }
          excerpt
        }
      }
    }
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`;
